    font-size: 18px;
    line-height: 21px;

}


.showAll {
    font-size: 14px;
    color: #8A9AAF;
    border: none;
    background-color: white;
    cursor: pointer;
}

.showAll:after {
    background-color: rgba(0, 102, 255, 0.9);;
    display: block;
    content: "";
    height: 3px;
    width: 0%;
    -webkit-transition: width .3s ease-in-out;
    -moz--transition: width .3s ease-in-out;
    transition: width .3s ease-in-out;
    color: #000000;
}
.showAll:hover:after,
.showAll:focus:after {
    width: 100%;
}


